*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.content{
    height: 82vh;
}

/* antd icons */


.switch-icons {
    border: 1px solid rgba(0, 0, 0, 0.677);
    border-radius: 5px;
    padding: 10px 15px;
    padding-bottom: 15px;
  }
  
  .anticon svg {
    font-size: 20px;
    cursor: pointer;
  }
  
  .active-icon {
    color: black;
  }
  .inactive-icon {
    color: gray;
  }
  
  .filter-tab .ant-select-selector {
    width: 150px !important;  
  }

